body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.hideMe {
    display: none;
}

.homepageToggleBar {
  width: 60%;
  margin: auto;
}

.pageToggleButton {
  width: 400px;
  padding: 1vh;
  margin-bottom: 15px;
}

.signupSpacer {
    width: 80%;
    padding: 5vh;
    background-color: aliceblue;
    text-align: justify;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4vh;
}

button.submitButton {
  margin: 2vh;
}

.signupBody {
    background-color: lightblue;
    min-height: 20vh;
    font-size: calc(5px + 2vmin);
    color: black;
    padding: 15vh;
    margin: 5vh;
    line-height: 5vh;
  }

P.alreadyMember {
 display: inline-block;
 margin: auto;
 padding-left: 20vh;
 font-size: 25px;
  }

input.form-control {
      margin-left: 15px;
  }
.pageSwitchBar {
    text-align: center;
    max-height: 5vh;
    margin: 0px;
}

A.pageToggle {
    color: white;
}
.hiddenValue {
    display: none;
}

.submitNewPost {
    text-align: center;
    margin: 15px;
}

.formItem {
    display: -webkit-inline-flex;
    display: inline-flex;
    float: left;
}

.descriptionBox {
    display: inline;
}

.makePostPageBox {
    padding: 0px;
    padding-left: 25px;
    padding-right: 25px;
}

.formLabel {
    font-weight: bolder;
}


.buy-btn {
  float: right;
  color: #e74944;
}

A.buyLink { color: black; text-decoration: none; }

A:link.buyLink { text-decoration: none }

A:active.buyLink { text-decoration: none }

A:hover.buyLink { background-color: transparent; text-decoration: underline; border: 0px; text-shadow: 2px 2px #b6b6b6;}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}


.formLabel {
    display: -webkit-inline-flex;
    display: inline-flex;  
    float: left;
    padding-right: 25px;
    padding-left: 25px;
}

.formItem {
    display: -webkit-flex;
    display: flex;
    padding-right: 25px;
    padding-left: 25px;
    float: left;
}

.pageSplit {
    border-width: 30px;
    margin-top: 8vh;
    width: 100%;
}
  ul.App-navbar {
    border-color: rgb(255, 169, 169);
    border-width: 2px;
    border-style: inset;
    position: -webkit-sticky;
    position: sticky;
    padding: 10px;
    background-color: #282c34;
    height: 10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: calc(12px + 2vmin);
    color: white;
    text-transform: uppercase;
  }

  li.menuLink {
    list-style-type: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 20px;
    font-size: calc(8px + 2vmin);
  }

  img.logo {
    width: 50px;
  }
  
  A:hover.logo { text-decoration: none; }

  A.menuLink { color: white }

  A:link.menuLink { text-decoration: none; padding: 15px;}

  A:active.menuLink { text-decoration: none; }

  A:hover.menuLink { color: black; text-decoration: none; background-color: rgb(197, 197, 197); border-radius: calc(8px + 2vmin); border-color: black; border-width: 1px; }

.landingBar {
    text-align: center;
    margin: auto;
    padding: 25px;
}

.landingTitle {
    font-size: 4vh;
    margin-left: 25px;
    margin-right: 25px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s ease-in-out;
          animation: App-logo-spin infinite 10s ease-in-out;
  height: 60px;
}

.App-header {
  background-color: #222;
  min-height: 10vh;
  color: white;
  margin: 0px;
  padding-top: 25px;
}

.App-body {
  margin-top: 5vh;
  text-align: center;
}

.App-footer {
  text-align: center;
  font-size: 25px;
  min-height: 10vh;
  margin-top: 45px;
  border: 2px;
  border-color: black;
  background-color: green;
  line-height: 80px;
  }

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

